.about-container{
    margin: 3rem 4rem;
    text-align: justify;
}

.hero-mid{
  height: 95vh;
}

.hero-mid::before{
  content: "";
    width: 100%;
    height: 95vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.7;
}


.about-container h1{
  padding-bottom: 1rem;
  font-weight: bold;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.about-container p{
    padding-bottom: 2rem;
    font-size: 1.2rem;
}