.header{
    font-size: 3rem;
    font-weight: bold;
}



.cardTitle{
    font-size: 1.5rem;
    font-weight: bold;
}

.pera{
    font-size: 1.1rem;
    text-align:justify;
}

.fullCard{
    border-radius: 8px;
    box-shadow: -1px 1px 62px -2px rgba(91, 26, 1, 0.14);

}

.t-image{
    width: 19rem;
     height:39rem;
     border-radius: 7px;
     overflow: hidden;
     cursor: pointer;
}

.t-image img{
    transition: 0.3s ease-in-out;

}

.t-image:hover img{
    transform: scale(1.3);
}

.categay{
    font-size: 2rm;
    text-align: start;
    font-weight: bold;
    margin: 20px;
}