.NavbarItems{
    display: flex;
    flex-direction: row;
    justify-content: center;
   
    /* align-items: center; */
    padding:0 0%;
    box-shadow: 0 5px 15px 0 rgba(138, 32, 3, 0.25);
    width: 80%;
    height: 70px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 9999;
    background-color:white;
}

.navbar-logo{
    color: #e84206;
    font-family: Arial, Helvetica, sans-serif;
 
    font-weight: bold;
    font-size: 2rem;
    cursor: pointer;


}

.NavbarItems h1{
    text-align: end;

}



.nav-menu{
    /* display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center; */
   
}

.nav-links{
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i{
    padding-right: 10px;
}

.nav-links:hover{
    background-color: #e84206;
    color: #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}

.nav-links-mobile{
    display: none;
}

button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

button:hover{
    background-color: black;
    color: white;
}

.nn{
    background-color: white;
    
}