.hero{
    width: 100%;
    height: 100vh;
    position: relative;
    
}

.hero::before{
    content: "";
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.7;
}

.hero-mid{
    width: 100%;
    height: 70vh;
    position: relative;
}

img{
    width: 100%;
    height: 100%;
    object-fit: cover;
   
}

.hero-text{
    position: absolute;
    top: 30%;
    left: 0;
    transform: translate(-50%, -50%);
    width: 100%;
}

.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background-color: rgba(227, 98, 23, 0.922);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0 5px 15px 0 rgba(0, 0, 0, .25);
}

.hero-text p{
    font-size: 1.3rem;
   padding: 0.5rem 0 2rem 0;
   color: #989595;
    font-weight: bold;
   
}

.hero-text .show{
    text-decoration: none;
    background: #fff;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #0c0c0c;
}

.hero-text .show:hover{
    color: #01959a;
}

.hero-text .hide{
  display: none;
}

.logoname img{
    height: 250px;
    width: 250px;
  
    


}

@media screen and (max-width:555px){
    .hero-text h1{
        padding: 10px 20px;
    }

    .hero-text p{
        font-size: 1.1rem;
       padding: 0 0 2rem 0;
    
       
    }

    .hero-text .show{
      
        padding: 0.6rem 1.1rem;
        border-radius: 4px;
        font-size: 1.1rem;
        
    }

    
}