.header{
    font-size: 3rem;
    font-weight: bold;
    color: rgb(255, 98, 0);
}



.cardTitle{
    font-size: 1.5rem;
    font-weight: bold;
}

.pera{
    font-size: 1.3rem;
    text-align: center;
}

.fullCard{
    border-radius: 8px;
    box-shadow: -1px 1px 62px -2px rgba(252, 89, 8, 0.19);
    background-color: rgb(14, 13, 13);
    color: white;
}

.t-image{
    width: 19.3rem;
     height:33rem;
     border-radius: 7px;
     overflow: hidden;
     cursor: pointer;
}

.t-image img{
    transition: 0.3s ease-in-out;

}

.t-image:hover img{
    transform: scale(1.3);
}