.destination{
    margin: 4rem 5rem;
}

.destination h1{
    font-size: 3rem;
    font-weight: bold;
}

.first-des{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.first-des-reverse{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
}

.des-text{
    text-align: start;
    font-size: 1.1rem;
}

.des-text p{
    text-align: justify;
    font-size: 1.2rem;
}

.des-text h2{
    padding-bottom: 1rem;
    font-weight: bold;

}

.secondH2{
    padding-bottom: 1rem;
    font-weight: bold;
    text-align: start;
}

.life img{
    width: 70%;
    height: 70vh;
    border-radius: 10px;
    position: relative;
   
}

.cycle h3{
    text-align: start;
    color: orange;
}

.cycle p{
    text-align: start;
}

.image img{
    
    height: 350px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 62px -10px rgba(163, 57, 4, 0.862);
}

.image img:nth-child(2){
    position: absolute;
    top: -10%;
    right: 0;
}

@media screen and (max-width: 850px){
    .destination{
        margin: 4rem 2rem;
    }
    .des-text{
        width: 100%;
    }
    .life img{
        width: 100%;
        height: 30vh;
        border-radius: 10px;
        position: relative;
    }
}
