.footer{
    padding: 4rem 2rem;
    background-color: black;
    color: white;
    box-shadow: -1px 1px 62px -8px rgba(123, 38, 2, 0.862);
}

.top h1{
    font-weight: bold;
    font-size: 2rem;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(255, 140, 0);
}

.top i{
    color: white;
    font-size: 2rem;
    margin-left: 1rem;
}

.top i:hover{
    color: #de5906;
}

.bottom{
    padding-top: 2rem;
    text-align: center;
}

.bottom div{
    display: flex;
    flex-direction: column;
}

.title{
    font-size: 1.3rem;
    padding: 1rem 0 .8rem 0;
    font-weight: bold;

}
.bottom a{
    text-decoration: none;
    color: rgb(169, 166, 166);
    padding-bottom: .4rem;
    font-size: 1.1rem;
}